<template>
  <div class="article box" @click="navigateTo">
    <div class="title-area" :style="'background-image:url(' + (article.poster ? article.poster : defaultImg) + ')'">
      <h2 class="title text-overflow" v-text="article.title"></h2>
    </div>
    <div class="article-content">
      <p class="content" v-text="article.abstract"></p>
    </div>
    <div class="info">
      <p>{{ article.author }}</p>
      <p>{{ calcArticleTime(article.date) }}</p>
    </div>
  </div>
</template>

<script>
import tools from '@/utils/public';

export default {
  name: 'articleList',
  data () {
    return {
      defaultImg: '/default/img3.png'
    }
  },
  props: [
    'article'
  ],
  mounted () {
    tools.setLazyLoadImg();
  },
  methods: {
    calcArticleTime (time) {
      return tools.calcTime(time);
    },
    navigateTo () {
      sessionStorage.setItem('articleinfo', JSON.stringify({
        title: this.article.title,
        date: this.article.date,
        poster: this.article.poster,
        id: this.article.filename,
      }));

      this.$router.push({
        name: 'article',
        params: {
          name: this.article.filename
        }
      })
    }
  }
}
</script>

