import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import article from '@/pages/article'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: {
        keepAlive: true,
        title: '我的圣西罗'
      }
    },
    {
      path: '/:name',
      name: 'article',
      component: article,
      meta: {
        keepAlive: false,
        title: 'xx'
      }
    }
  ]
})