<template>
  <page-box>
    <Header :title="title" :img="article.poster"></Header>
    <div class="my-info">
      <img :src="avatar" alt="">
      <p>{{ time }}</p>
    </div>
    <div class="main-area">
      <loading class="article-loading" v-if="!showArticle"></loading>
      <transition name="fade">
        <page-padding class="article-body markdown-body" v-show="showArticle" v-html="compiledMarkdown">
        </page-padding>
      </transition>
    </div>
  </page-box>
</template>

<script>
import Header from '@/components/Header'
import Loading from '@/components/Loading'
import pageBox from '@/components/PageBox'
import pagePadding from '@/components/PagePadding'

import tools from '@/utils/public'
import hljs from 'highlight.js'
import marked from 'marked'

import data from '@/data'

export default {
  name: 'articlePage',
  components: {
    Header, Loading, pageBox, pagePadding
  },
  data () {
    return {
      showArticle: false,
      article: sessionStorage.getItem('articleinfo') ? JSON.parse(sessionStorage.getItem('articleinfo')) : {},
      content: ''
    }
  },
  computed: {
    title() {
      return this.article.title ? this.article.title : '标题'
    },
    poster() {
      return this.article.poster ? this.article.poster : '/default/img4.png'
    },
    avatar() {
      return sessionStorage.getItem('sansiroavatar') ? sessionStorage.getItem('sansiroavatar') : '/default/default.png'
    },
    time() {
      return this.article.date ? tools.calcTime(this.article.date) : tools.calcTime('1995-06-02 12:12:12');
    },
    compiledMarkdown() {
      return marked(this.content)
    }
  },
  mounted () {
    tools.posTop();
    this.setMarkDown();

    this.getArticleInfo(this.$route.params.name).then(() => {
      tools.setLazyLoadImg();
    }); 
  },
  methods: {
    getArticleInfo (name) {
      return Promise.all([data.getArticleContent(name), data.getArticleInfo(name)]).then(data => {
        this.showArticle = true;
        this.content = data[0];
        Object.assign(this.article, data[1]);
      })
    },
    setMarkDown () {
      marked.setOptions({
        renderer: new marked.Renderer(),
        gfm: true,
        tables: true,
        breaks: false,
        pedantic: false,
        sanitize: true,
        smartLists: true,
        smartypants: false,
        highlight: function(code) {
          return hljs.highlightAuto(code).value
        }
      });
    }
  }
}
</script>

