<template>
  <div class="home">
    <div class="area box">
      <Header :title="msg" :img="backimg"></Header>
      <div class="my-info">
        <img :src="avatar" alt="">
        <p>{{ username }}</p>
      </div>
    </div>

    <loading v-if="!showList" class="home-loading box"></loading>

    <transition name="fade">
      <div class="article-area" v-show="showList">
        <article-list v-for="(todo, index) in articleList" v-bind:key="index" :article="todo"></article-list>

        <load-more class="home-more box" v-on:moreInfo="addToList"></load-more>
      </div>
    </transition>

    <div class="info"></div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import Loading from '@/components/Loading'
import LoadMore from '@/components/LoadMore'
import ArticleList from '@/components/ArticleList'

import data from '@/data'

export default {
  name: 'Home',
  components: {
    Header, ArticleList, LoadMore, Loading
  },
  data () {
    return {
      showList: false,
      articleList: [],
      msg: 'Welcome to sansiro.me',
      backimg: '/default/img4.png',
      avatar: '/default/default-head.jpg',
      username: 'sansiro'
    }
  },
  mounted() {
    this.getHomePageInfo();
  },
  methods: {
    getHomePageInfo() {
      data.getConfig().then(data => {
        this.showList = true;
        // console.log(data);
        this.msg = data.title;
        this.backimg = data.backimg;
        // this.avatar = data.avatar;
        // window.avatar = data.avatar;

        sessionStorage.setItem('sansiroavatar', data.avatar);
        sessionStorage.setItem('sansiroinfo', JSON.stringify(data));

        this.username = data.username;
      }).then(() => {
        this.getArticleList();
      })
    },
    getArticleList() {
      data.getArticleList().then(data => {
        // this.addToList(data.list);
        this.articleList = data;
      })
    },
    addToList(data) {
      this.articleList = this.articleList.concat(data);
    }
  }
}
</script>

