const exportsRes = {
  calcTime(time, size = 'big') {
    var str = String(time).replace(/-/g,"/");
  
    var newSS = new Date(str);
  
    var month = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  
    let res;
    if(size == 'big') {
      res = month[newSS.getMonth()] + " " + newSS.getDate() + "，" + newSS.getFullYear() + "\t" + this.getMoon(newSS.getHours());
    } else {
      res = month[newSS.getMonth()] + " " + newSS.getDate() + " " + this.getMoon(newSS.getHours());
    }
  
    return res;
  },
  setLazyLoadImg() {
    let windowHeight = window.innerHeight;
    let i, scroll, imgs, imgHeight;

    let load = () => {

      scroll = document.documentElement.scrollTop || document.body.scrollTop;
      imgs = document.getElementsByClassName('lazyload');
  
      for (i = 0; i < imgs.length; i++) {
        imgHeight = imgs[i].offsetTop;
  
        if (imgHeight < windowHeight + scroll) {
          // "https://sansiro.me" + 
          imgs[i].src = imgs[i].getAttribute('data-src');
          imgs[i].className = imgs[i].className.replace('lazyload','lazyloaded')
        }
      }
    }
  
    load();
  
    window.addEventListener('scroll', this.throttle(load, 200));
    // window.onscroll = this.throttle(load, 300);
  },
  getMoon(hour) {
    var res;
    if(hour >= 0 && hour < 6) {
      res = "凌晨";
    } else if(hour < 9) {
      res = "早晨";
    } else if(hour < 11) {
      res = "上午";
    } else if(hour < 15) {
      res = "中午";
    } else if(hour < 19) {
      res = "下午";
    } else {
      res = "晚上";
    }
  
    return res;
  },
  posTop() {
    document.documentElement.scrollTop = window.pageYOffset = document.body.scrollTop = 0;
  },
  throttle(func, delay) {
    let timer;

    return () => {
      if(timer) return;

      func();
      timer = setTimeout(() => {
        clearTimeout(timer);
        timer = null;
      }, delay);
    }
  }
}

// module.exports = exportsRes;

export default exportsRes;

