import Vue from 'vue'
import App from './App.vue'
import router from './router'

import './assets/mixin/topic.scss';

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    let title;

    if(to.meta.title == 'xx') {
      title = sessionStorage.getItem('articleinfo') ? JSON.parse(sessionStorage.getItem('articleinfo')).title : '文章';
    }
    else {
      title = to.meta.title;
    }

    document.title = title + ' － SANSIRO.ME';
  }
  next();
})