import axios from 'axios'

const json = {
  obj: {},
  list: [],
  size: 10,
  config: {},
  getArticleList() {
    return new Promise((res, rej) => {
      axios.get('./article/all.json').then(data => {
        if(data.status == 200) {
          // this.list = JSON.parse(JSON.stringify(data.data)).list;
          this.obj = data.data.list;
          this.list = Object.values(data.data.list);
          if(this.list.length < this.size) {
            res(this.list)
          } else {
            res(this.list.slice(0, this.size));
          }
        }

        rej('error');
      })
    })
  },
  getConfig() {
    return new Promise((res, rej) => {
      axios.get('./config/config.json').then(data => {
        if(data.status == 200) {
          this.size = data.data.pageSize;
          this.config = data.data;
          res(data.data);
        }

        rej('error');
      })
    })
  },
  getArticleContent(name) {
    return new Promise((res, rej) => {
      axios.get(`./article/list/${name}.md`).then(data => {
        if(data.status == 200) {
          res(data.data);
        }

        rej('error');
      })
    })
  },
  getArticleInfo(name) {
    return new Promise((res, rej) => {
      // axios.get(`./article/list/${name}/${name}.json`).then(data => {
      //   if(data.status == 200) {
      //     res(data.data);
      //   }

      //   rej('error');
      // })

      res(this.obj[name]);
    })
  },
  getLoadMoreList(num) {
    return new Promise((res, rej) => {
      if(this.list.length <= this.size * Number(num)) {
        rej('no more');
      }
      else if(this.list.length < this.size * (Number(num) + 1)) {
        res({
          enough: false,
          list: this.list.slice(this.size * Number(num), this.list.length)
        });
      }
      else {
        res({
          enough: true,
          list: this.list.slice(this.size * Number(num), this.size * (Number(num) + 1))
        });
      }
    })
  }
}

export default json;