<template>
  <div class="loadmore" @click="loadMore">
    <span v-if="loadmoreState == 0">点击加载更多</span>
    <span v-else-if="loadmoreState == 1"><i class="moreloading icon-Loading"></i> 加载中...</span>
    <span v-else>没有更多了...</span>
  </div>
</template>

<script>
import data from '@/data'

export default {
  data () {
    return {
      loadmoreState: 0,
      loadnum: 1
    }
  },
  methods: {
    loadMore() {
      if(this.loadmoreState) {
        return;
      }

      this.loadmoreState = 1;
      
      data.getLoadMoreList(this.loadnum).then(data => {
        this.resultEmit(data.list);

        if(data.enough) {
          this.loadnum++;
          this.loadmoreState = 0;
        } else {
          this.loadmoreState = 2;
        }
      }).catch(msg => {
        this.loadmoreState = 2;
      })
    },
    resultEmit(data) {
      this.$emit('moreInfo', data);
    }
  }
}
</script>

<style lang="scss" scoped>
.loadmore {
  line-height: 50px;
  text-align: center;
  cursor: pointer;
  font-size: 14px;
  color: #797979;
  transition: .2s;

  &:hover {
    color: #000;
    background-color: rgba(91, 155, 120, 0.1);
  }

  .moreloading {
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    animation: loading 2s linear infinite;
  }

  @keyframes loading {
    0% {
      transform: rotate(0);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
</style>
